import React from 'react'
import Button from '@material-ui/core/Button';
import AddIcon from '@material-ui/icons/Add';
function CreateNote(props) {
  const [expand,setExpand]=React.useState(false)
  const [note,setNote]=React.useState({
    title:'',
    content:'',
  })
  const InputEvent=(event)=>{
    
    const {name,value}=event.target
    setNote((prevData)=>{
      return{
        ...prevData,
        [name]:value,

      }
    })
  }
  const addEvent=()=>{

    props.passNote(note);
    setNote({
    title:'',
    content:'',
    })
  }
  const expandIt=()=>
  {
    setExpand(true)
  }
  const btNormal=()=>
  {
    setExpand(false)
  }
  return (
      
    <>
    <div className='main_note'>
        <form>
            {expand? <input type='text' name="title" value={note.title} onChange={InputEvent} placeholder='Title' autoComplete='off'/> :null}
            <textarea rows='' column='' name="content" value={note.content} onChange={InputEvent} placeholder="Write a note here" onClick={expandIt} onDoubleClick={btNormal}>
            </textarea>
            {expand? <Button onClick={addEvent}>
                <AddIcon className='plus_sign'/>
            </Button> :null}
        </form>
    </div>
    </>
  );
}

export default CreateNote;